import React from "react";
import { filterCharacters } from "../services/CharacterService";
import { Grid } from "@mui/material";
import { Paper, Typography, Switch } from "@mui/material";
import Avatar from "@mui/material/Avatar";
import MUIDataTable from "mui-datatables";

export default function Character(props) {
  const [page, setPage] = React.useState(1);
  const [characters, setCharacters] = React.useState([]);
  const [filterContent, setFilterContent] = React.useState({
    status: "",
    species: "",
  });

  const handleSwitch = (event) => {
    props.setChecked(event.target.checked);
  };

  const columns = [
    {
      name: "id",
      label: "Id",
      options: {
        filter: false,
        sort: true,
      },
    },
    {
      name: "name",
      label: "Name",
      options: {
        filter: false,
        sort: true,
      },
    },
    {
      name: "image",
      label: "Avatar",
      options: {
        customBodyRender: (value) => {
          return (
            <div style={{ display: "flex", alignItems: "center" }}>
              <Avatar
                src={value}
                alt="Avatar"
                sx={{ height: "100px", width: "100px" }}
              />
            </div>
          );
        },
        filter: false,
        sort: false,
      },
    },
    {
      name: "species",
      label: "Species",
      options: {
        filter: true,

        sort: false,
      },
    },
    {
      name: "status",
      label: "Status",
      options: {
        filter: true,
        sort: false,
      },
    },
  ];

  const handleTheFiltering = React.useCallback(async () => {
    const filterededData = await filterCharacters(filterContent, page);
    setPageCount(filterededData.info.count);
    setCharacters([...filterededData.results]);
  }, [filterContent, page]);

  React.useEffect(() => {
    handleTheFiltering();
  }, [handleTheFiltering]);

  const [pageCount, setPageCount] = React.useState(826);
  const options = {
    filterType: "dropdown",
    count: pageCount,
    rowsPerPage: 20,
    rowsPerPageOptions: [20],
    elevation: 5,
    pages: 2,
    serverSide: true,
    onTableChange(action, tableState) {
      if (action === "changePage") {
        setPage(tableState.page + 1);

        handleTheFiltering();
      }
    },
    onFilterChange(changedColumn, filterList) {
      setFilterContent((prev) => ({
        ...prev,
        species: filterList[3],
        status: filterList[4],
      }));
    },
  };

  return (
    <Grid>
      <Grid container alignItems="center">
        <Grid item ml={3} mt={2}>
          <Typography>
            Light
            <Switch
              checked={props.checked}
              onChange={handleSwitch}
              inputProps={{ "aria-label": "controlled" }}
            />
            Dark
          </Typography>
        </Grid>
      </Grid>
      <Grid
        container
        mt={3}
        direction="row"
        alignItems="center"
        justifyItems="center"
        justifyContent="center"
      >
        <br />
        <Grid item mt={5} sm={12} md={12} sl={12} lg={12}>
          <Paper elevation={7}>
            <MUIDataTable
              title={"Character List"}
              data={characters}
              columns={columns}
              options={options}
            />
          </Paper>
        </Grid>
      </Grid>
      <Grid container direction="column" alignItems="center"></Grid>
    </Grid>
  );
}
