export const getCharacters = async (page) => {
  const characterFetch = await fetch(
    `https://rickandmortyapi.com/api/character/?page=${page}`
  );
  const characterData = await characterFetch.json();
  //console.log(characterData);
  return characterData.results;
};

export const getCharactersById = async (num) => {
  const characterFetch = await fetch(
    "https://rickandmortyapi.com/api/character/" + num
  );
  const characterData = await characterFetch.json();
  //console.log(characterData);
  return characterData;
};

export const filterCharacters = async (filterContent, page) => {
  try {
    const link = `https://rickandmortyapi.com/api/character/?page=${page}&status=${filterContent.status}&species=${filterContent.species}`;
    //console.log(link);
    const characterFetch = await fetch(link);

    const filteredData = await characterFetch.json();
    return filteredData;
  } catch (e) {
    console.log(e);
  }
};
